<template>
  <div class="captured-image-container">
    <h1>Decorate your frame</h1>
    <div class="camera-result-container">
      <div class="camera-frame-container">
        <p>Image Preview</p>
        <div class="camera-frame" id="cameraMainFrame" v-bind:style="{'background': (selectedColor === 'polka' || selectedColor === 'winter') ? 'url(' + frameColor + ')' : frameColor}">
          <div id="my_result" class="camera-captured">
            <img :src="image" />
          </div>
        </div>
      </div>
      <div class="frame-theme-selector">
        <p>Select Frame Color:</p>
        <ul class="frame-color-list">
          <li @click="changeFrameTheme('burlywood')">
            <div class="select-color default"></div>
          </li>
          <li @click="changeFrameTheme('green')">
            <div class="select-color green"></div>
          </li>
          <li @click="changeFrameTheme('blue')">
            <div class="select-color blue"></div>
          </li>
          <li @click="changeFrameTheme('black')">
            <div class="select-color black"></div>
          </li>
          <li @click="changeFrameTheme('yellow')">
            <div class="select-color yellow"></div>
          </li>
          <li @click="changeFrameTheme('pink')">
            <div class="select-color pink"></div>
          </li>
          <li @click="changeFrameTheme('violet')">
            <div class="select-color violet"></div>
          </li>
          <li @click="changeFrameTheme('orange')">
            <div class="select-color orange"></div>
          </li>
          <li @click="changeFrameTheme('polka')">
            <div class="select-color polka"></div>
          </li>
          <li @click="changeFrameTheme('winter')">
            <div class="select-color winter"></div>
          </li>
        </ul>
      </div>
    </div>
    <br>
    <button class="button-cancel" @click="reset">Retake</button>
    <button class="button-confirm" @click="renderCanvas">Preview</button>
  </div>
</template>
<script>
import html2canvas from 'html2canvas';
import polkaPic from '@/assets/polka-dots.png'
import winterPic from '@/assets/winter.jpg'

export default {
  name: 'Preview',
  props: {
    image_file: String
  },
  data() {
    return {
      image: '',
      frameColor: 'burlywood',
      selectedColor: 'burlywood'
    }
  },
  mounted() {
    this.image = this.image_file;
  },
  methods: {
    reset() {
      this.$router.push({name: 'Home'});
    },
    changeFrameTheme(color) {
      this.selectedColor = color;
      switch(color) {
        case 'burlywood':
          this.frameColor = color;
          break;
        case 'green':
          this.frameColor = color;
          break;
        case 'blue':
          this.frameColor = color;
          break;
        case 'black':
          this.frameColor = color;
          break;
        case 'yellow':
          this.frameColor = color;
          break;
        case 'pink':
          this.frameColor = color;
          break;
        case 'violet':
          this.frameColor = color;
          break;
        case 'orange':
          this.frameColor = color;
          break;
        case 'polka':
          this.frameColor = polkaPic;
          break;
        case 'winter':
          this.frameColor = winterPic;
          break;
      }
    },
    renderCanvas() {
      let that = this;

      html2canvas(document.getElementById('cameraMainFrame')).then(function(canvas) {
        let toBase64 = canvas.toDataURL();
        that.$router.push({name: 'Finale', params:{image_file: toBase64}});
      });
    },
  }
};
</script>
