    height:350px;
    padding-top: 15px;
  }

  .camera-frame {
    height: 320px;
    width: 350px;
    margin: 0 auto;
  }

  .camera-frame-container {
    float: left;
    width: 50%;
  }

  .frame-theme-selector {
    float: right;
    width: 50%;
  }

  .frame-color-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    list-style-type: none;
    width: 225px;
    margin: 0 auto;
    margin: 0 auto;
    padding: 0;
  }

  .select-color {
    height: 25px;
    width: 25px;
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 10px;
    background: burlywood;
    cursor: pointer;
  }

  .select-color.default {background: burlywood;}
  .select-color.green {background: green;}
  .select-color.blue {background: blue;}
  .select-color.black {background: black;}
  .select-color.yellow {background: yellow;}
  .select-color.pink {background: pink;}
  .select-color.violet {background: violet;}
  .select-color.orange {background: orange;}
  .select-color.polka {
    background: url('../assets/polka-dots.png');
    background-size: cover;
  }
  .select-color.winter {
    background: url('../assets/winter.jpg');
    background-size: cover;
  }

  @media only screen and (min-width: 0) and (max-width: 991px) {
    .camera-result-container {
      flex-direction: column;
      width: 95%;
      margin: 0 auto;
    }

    .camera-frame-container,
